/**
*$attrs和$listeners还有inheritAttrs的使用(创建高阶组件)
*watch 通过 computed返回一个对象 监听多个data里面的属性
*自定义v-model
 *//

<template>
    <div class="parent">
        <son :name="name" :age="age" :sex="sex" :city="city" :test="test" @grandson="getGrandson"  @grandson1="getGrandson1"  :myvalue='inputval' @myinput="val =>  inputval = val"></son>
        <!-- <son :name="name" :age="age" :sex="sex" :city="city" :test="test" @grandson="getGrandson"  @grandson1="getGrandson1"  v-model="inputval" ></son> -->
        <div>inputval:{{inputval}}</div>
        <div>grandsonval:{{grandsonval}}</div>
        <div>grandsonval1:{{grandsonval1}}</div>
    </div>
</template>

<script>
import son from './son'
export default {
    provide(){
        return {
            parent:'父组件的值'//不是响应式的
        }
    },
    data() {
        return {
           name:'张三',
           age:'18岁',
           sex:'男人',
           test:'测试',
           city:'重庆' ,
           grandsonval:'',
           grandsonval1:'',
           inputval:'inputval',
        }
    },
    // inheritAttrs:false,
     components:{
        son,
    },
    methods: {
        getGrandson(val){
            this.grandsonval =val
        },
        getGrandson1(val){
            this.grandsonval1 =val
        }
    },
    mounted() {
        setTimeout(()=>{
          this.grandsonval = '定时器触发了';
          this.name = '张三变成王五了'
        },3000)
    },
    watch: {
        grandsonObj:{
            handler(n,o){
                if(n.grandsonval !== o.grandsonval){
                    console.log('grandsonval改变了')
                }
                if(n.grandsonval1 !== o.grandsonval1){
                    console.log('grandsonval1的值改改改改改变了')
                }
            }
        }
    },
    computed: {
        grandsonObj(){
            const {grandsonval,grandsonval1} = this;
            return {
                grandsonval,
                grandsonval1
            }
        }
    },
}
</script>

