<template>
    <div class="son">
        <!-- <grand-son></grand-son> -->
        <div>city:{{city}}</div>
        <!-- $attrs 指的组件不被包含props -->
        <p>sonattrs:{{$attrs}}</p>
        <p>sonprops:{{$props}}</p>
        <input type="text" :value="myvalue" @input="inputval">
        <p>value is :{{datavalue}}</p>
        <!-- $attrs==={ "name": "张三", "age": "18岁", "sex": "男人" } 没有包含props 的数据 -->
        <!-- $props==={ "myvalue": "inputval", "city": "重庆" } -->
        <!-- b组件绑定attrs listeners 在a组件中，监听c组件触发的事件。就能把c组件发出的数据，传递给a组件 -->
        <grand-son v-bind="$attrs"  v-on="$listeners" ></grand-son>
    </div>
</template>

<script>
import grandSon from './grandson'
export default {
    model: {
        prop: 'myvalue', //prop 要和 props 里面的值对应
        event: 'myinput'
    },
    props:['city','myvalue'],
    data() {
        return {
            datavalue:this.value
        }
    },
    inheritAttrs:false,//改为false 这儿没有指定的prop属性 就不会作为attribute显示再标签上
    components:{
        grandSon,
    },
    computed: {
        
    },
    methods: {
        inputval($event){
           this.datavalue = $event.target.value;
           this.$emit('myinput',this.datavalue)
        }   
    },
}
</script>

