<template>
    <div class="grandson">
        <div>name:{{name}}</div>
        <div>sex:{{sex}}</div>
        <div>age:{{age}}</div>
        <!-- <div>我是grandsoncity:{{city}}</div> -->
        <div>我是grandsonattrs:{{$attrs}}</div>
        <div>我是grandsonprops:{{$props}}</div>
        <div>inject:{{parent}}</div>
        
        <button @click="setEmit">点我传值</button>
    </div>
</template>

<script>
export default {
    props:['name','sex','age','city'],
     inheritAttrs:true,//改为false 这儿没有指定的prop属性 就不会作为attribute显示再标签上
     inject:['parent'],
    data() {
        return {
            msg:'我是grandson的值',
            msg1:'我是grandson1得得得的值',
        }
    },
    methods: {
        setEmit(){
            this.$emit('grandson',this.msg)
            this.$emit('grandson1',this.msg1)
        }
    },
}
</script>

<style lang="stylus" scoped>
.grandson 
    background pink
</style>
